<template>
    <div class="success">
        <img src="../../../static/images/success.jpg" alt="">
        <span class="close">关闭</span>
    </div>
</template>
<script>
export default {};
</script>
<style lang="scss" scoped>
.success {
  width: 23.5rem;
  img {
    position: relative;
    width: 100%;
    height: 100%;
  }
  .close {
    position: absolute;
    border: 2px solid rgba(170, 170, 170, 1);
    border-radius: 0.5rem;
    height: 1.8rem;
    color: #aaa;
    padding: 0 3rem;
    margin: -16.4rem 0 1.1rem 6.8rem;
    line-height: 1.8rem;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
  }
}
</style>



